import React from 'react'


class OogenesisText extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			korDisplay:"inherit",
	     	engDisplay:"none"
	  	}
	}

	componentDidUpdate(prevProps) {
	   if (this.props.lang !== prevProps.lang) {
			this.setLang();
	   }
	}
	setLang(){
		if(this.props.lang == "kor"){
			this.setState({
				korDisplay:"inherit",
				engDisplay:"none"
			})
		}else{
			this.setState({
				korDisplay:"none",
				engDisplay:"inherit"
			})
		}
	}


	render() {
		return (
			<div>
				<div style={{display:this.state.korDisplay}}>
					<div className="fictionTitle" style={{fontSize:"30px"}}>가능성의 창문들: 유성생식의 관점에서 예술 생산을 고쳐 쓰기</div>
				<p className="fictionAuthorTitle">윤원화</p>
					<div className="diagonalPFiction">
					김나희의 &lt;대디 레지던시&gt; 프로젝트는 ‘나는 앞으로 어떤 가족을 만들 수 있을까?’라는 단순하지만 생산적인 질문에서 출발한다. 이 질문은 교리문답처럼 그에 답하는 전형적인 이미지가 있다. 그러나 가족은 성관계가 사회적 관계로 확장되는 전환점이자 성선택에 의한 진화의 최전선으로서 고유한 역동성을 내포한다. 역사적으로 다양한 인간 사회들이 저마다 가족을 신화화한 것은 생물학적 조건과 문화적 조건이 자의적으로 결합된 가족이라는 가변적 단위를 안정화할 필요가 있었기 때문이다. 반면 김나희는 가족을 사회의 구성요소이기 이전에 개인을 둘러싼 관계의 배치로 보고 그 변동성을 활성화할 수 있는 방안을 모색한다. 말하자면, 가족을 어떻게 미래가 있는 방향으로 개방할 수 있을까? 이것이 질문이다. 여기서 중요한 것은 나의 가족이 어떻게 미래에 생존할 수 있는가가 아니라 가족의 형태가 어떻게 현재보다 나은 미래를 향한 입구로서 재설계될 수 있는가다. <br /><br />
					가족을 SF적인 의미에서 다른 가능세계들로 연결된 포털로 개발하려는 시도로서 &lt;대디 레지던시&gt;는 단일하고 일관성 있는 유토피아적 미래의 비전에 집착하기보다 현재 상태에서 그와 다른 상태로의 분기점들을 신속하게 증식하는 데 집중한다. 현실에 존재하는 것은 아직 존재하지 않는 것을 위한 도구로 변용된다. 애초에 이 프로젝트가 가족이라는 오래된 개념을 폐기하지 않고 엄마, 아빠, 아이라는 기본 구성을 유지한 것은 미래가 현재로 침투할 입구가 필요했기 때문이다. 원칙적으로 &lt;대디 레지던시&gt;는 아직도 ‘남자와 여자를 짝지어 놓으면 자연스럽게 아이가 생긴다’라는 식으로 얼렁뚱땅 말해지는 가족이라는 프로그램의 합리적 갱신을 지향한다. 프로그램은 어떤 임무를 달성하는 데 필요한 일련의 절차를 체계적으로 정리한 지시문들의 집합이다. 한 명의 아이 또는 새로운 인간을 산출하는 작은 사회를 구성하는 일을 프로그래머의 합리성에 입각하여 접근한다면 어떤 가족 계획이 도출될까? <br /><br />
					실제로 김나희는 2019년 &lt;대디 레지던시&gt;의 첫번째 온라인 전시에서 미래의 가족을 위한 소스 코드를 오픈 소스 소프트웨어 개발 플랫폼인 깃허브를 통해 공개했다. Family.app이라고 명명된 이 프로그램의 초안에서 ‘엄마’, ‘아빠’, ‘아이’는 아직 정의되지 않은 텅 빈 항목들에 가깝다. 이 단계에서 확정된 계획 또는 원칙이라면 정해진 기한 내에 아이가 태어나야 한다는 것과 아빠가 가능한 한 분산되어야 한다는 것뿐이다. 생물학적 아빠와 양육하는 아빠를 분리하고 후자를 3-6개월 정도 근무하는 다수의 레지던트로 충원하는 ‘대디 레지던시’ 시스템은 아빠 기능이 특정 개인에게 집중되는 것을 방지한다. 이는 생산 수단의 소유자이자 프로젝트 발기인인 엄마의 권한을 침해할 수 있는 잠재적 위험 요소로서 아빠가 관리될 필요가 있다는 판단을 반영한다. 그렇지만 양육자로서 엄마와 아빠의 역할이 미리 결정된 것은 아니다. 다만 ‘엄마’와 ‘아빠’라는 별개의 항목을 상정함으로써 새로운 개체를 형성하는 과정에서 엄마와 구별되는 다른 조력자들이 필요하다는 유성생식의 기본 원리를 재확인한다. <br /><br />
					이러한 프로그램 설계는 김나희의 자의적 판단이 아니라 Family.app의 상위 범주인 nahee.app의 소관으로  설정되는데, 이는 엄마 기능과 ‘가족’이라는 시스템의 관리자를 분리한다는 것을 의미한다. 기능적으로 말해서 &lt;대디 레지던시&gt;는 김나희의 연장선에서 ‘아이를 만드는 자’와 ‘가정을 만드는 자’를 별도로 객체화한다. 김나희에서 유래하는 다수의 기능들의 집합체로서 nahee.app은 고유한 역사와 성향들을 지닌 하나의 군단을 이룬다. nahee.app이라는 인스타그램 계정과 웹사이트와 깃허브 저장소가 있고 다시 그 안에서 작성되고 변환되는 수많은 버전의 nahee.app들이 있다. 이들은 웹 상에서 김나희와 관련된 자료를 모아 놓는 가상적 폴더로 시작하여 주로 성관계에서 김나희의 감각운동적 작동을 기록하고 분석하는 일련의 문서들로 이어졌으며, 현재는 성적 상호작용을 동반하는 사회적 상황에서 김나희의 수행을 최적화하는 생명기술적 인터페이스를 개발하는 일종의 자기-연구실로 진화했다. <br /><br />
					한동안 nahee.app은 김나희의 작가 기능과 그 생산물들을 양쪽 모두 아우르는 느슨한 범주로서 작가의 분신들이 증식하는 무성생식적 또는 난세포형성적 단계에 머물렀다. 이런 맥락에서 &lt;대디 레지던시&gt;는 nahee.app의 자기 지시적 운동이 좀 더 외부를 향해 개방되는 새로운 발달 단계로 볼 수 있다. 자기의 무수한 변종들과 함께 쾌적하고 만족스러운 성관계의 형태들을 탐구해온 nahee.app의 우주에서 ‘엄마’, ‘아빠’, ‘아이’는 모두 생소한 개념이다. 김나희는 자기를 급진적으로 재정의하고 자기 외부로 확장할 것을 요구하는 가족 만들기의 과제를 작가 자신의 새로운 프로젝트로 입안하면서, 이를 한 사람의 사적인 문제가 아니라 여러 사람이 함께 생각하고 원한다면 직접 참여할 수도 있는 사회기술적 문제로 고쳐 쓴다. 여기에는 복합적인 충동들과 때로 모순되는 감정들이 뒤엉켜 있다. 놀라움의 추구와 안전함의 요구 사이에서 정념을 발산하는 출구이자 그런 비논리적인 블랙박스에 대한 분석과 제어를 목적으로 하는 가상 기계로서 nahee.app은 어떤 미래를 만들 수 있을까? <br /><br />
					프로젝트의 초기 단계인 현 시점에서 &lt;대디 레지던시&gt;는 크게 두 방향으로 열려 있다. 하나는 김나희의 파생물들이 상연하는 사고 실험으로서 미지의 사변적 우주로 더욱 깊이 들어가는 것이고, 다른 하나는 현실에 영향을 끼칠 수 있는 실질적 계획으로서 지상의 세계와 직접 충돌하는 것이다. 글쓰기, 프로그래밍, 전시, 렉처 퍼포먼스 등을 통한 예술적 실천은 이 상반된 운동을 상호적으로 촉진하고 보호한다. 그러나 2026년으로 예정된 아이의 탄생이 가까워지면 &lt;대디 레지던시&gt;는 어떤 식으로든 예술의 영역을 넘어 현실의 법적, 기술적, 생물학적 조건들과 부딪힐 것이다. 이 아이의 미래, 또는 ‘아이’라는 이름의 미래는 그 사이에 김나희가 얼마나 많은 조력자들을 연합해낼 수 있는가에 달렸다. 조력자는 사람일 수도 있고 인공물일 수도 있으며, 물리적 실체일 수도 있고 사변적 객체일 수도 있다. <br /><br />
					이는 SF적인 설정만은 아니다. 예를 들어 집합적 신격으로서의 조상님은 사변적 객체지만 제사상이라는 물리적 실체를 인터페이스 삼아 현실에 관여한다. 김나희는 이 구조를 시간의 반대 방향으로 돌려 미래의 아이들을 소환하기 위한 일시적 무대들을 세운다. 최근 &lt;대디 레지던시&gt;에서 분기한 &lt;대자궁&gt; 시리즈는 생식세포를 데이터 전달자로 전용하여 생식기관 기반의 통합적 미디어 시스템을 구축한 미래 사회를 탐구한다. 이곳은 단지 여성 생식세포의 기술적 활용성이 더 높았기 때문에 자연스럽게 여성의 생리주기를 계절의 주기처럼 따르는 여성 중심의 사회가 되었으며, 이러한 시간적 질서를 더욱 체계화하여 국가 전체가 세포 수준의 커뮤니케이션을 통해 ‘대자궁’의 시간에 동기화되는 기술적 유기체로 진화했다. 조애나 러스가 창조한 페미니스트 유토피아 ‘와일어웨이’의 이름을 빌려온 이 평행우주는 현재의 기술화된 가부장적 사회의 역상이 될 수도 있고 어쩌면 부정의 대상으로라도 현재와 관계 맺지 않는 순수한 미래의 창이 될 수도 있다. 옳고 그름이나 바람직함을 판단하기 전에 일단 ‘원래 그런 것’에 한정되지 않는 가능성의 지평을 전부 열어 보고 싶다는 열망은 김나희의 피조물들을 움직이는 공통의 명령이다. 가능한 모든 삶의 형태들이 미래의 아이들이 되어 쇄도한다면, 우리는 어떻게 그들을 맞이해야 할까? <br /><br />  
					</div>
				</div>
				<div style={{display:this.state.engDisplay}}>
					
					<div className="fictionTitle" style={{fontSize:"30px"}}>Windows of Possibilities: Rewriting Art Production with the Perspective of Sexual Reproduction</div>
				<p className="fictionAuthorTitle">Wonhwa Yoon</p>
					<div className="diagonalPFiction">
					<br/><br/>
					<p>Nahee Kim’s Daddy Residency starts from the simple yet productive question, “What kind of family can I make in the future?” Like a catechism, there is a typical image that answers. A family is the turning point where sexual relations expand into the social realm, and connotes an intrinsic dynamic as the front line of evolution through sexual selection. Throughout history it was necessary that the family, an arbitrary consequence of biological and cultural conditions, was stabilized through its mythification. However, as to Nahee the family is an arrangement of relationships surrounding an individual before it is a component of society, she seeks out methods to activate its volatility. Put simply, the question is: How can the family be opened in the direction of the future? What matters here is not how one’s family can survive, but how its structure can be reengineered to act as an entryway towards a better tomorrow.</p>
					<br/>
					<p>As an attempt to develop the family into a science fiction-esque portal to possible worlds Daddy Residency focuses on the rapid proliferation of intersections that diverge from the status quo, rather than obsessing over a singular and consistent version of a utopian future. What exists in reality is mutated into a tool for what does not yet exist. The reason this project maintains the outdated concept of family and its elements of mother, father, and child in the first place is because the future it conceives requires an entrance into the present. In principle Daddy Residency supports the rational renewal of the family program - one that haphazardly designates that when a man and woman is paired, a child naturally occurs. A program is a systematic set of directives that organize the steps required to accomplish a task. When the work of constructing a small society that produces one child or new human is approached based on the rationality of the programmer, what will the resulting family plan look like?</p>
					<br/>
					<p>During the first online exhibit of Daddy Residency in 2019, Nahee revealed the source code for the family of the future on the open source software development platform GitHub. In the program draft titled Family.app, ‘mother,’ ‘father,’ and ‘child’ are empty items that have not yet been defined. The only statutes established at this stage are that a child must be born within the set deadline, and that the fathers should be as spread out as possible. The Daddy Residency system, which separates the biological and parenting roles and fulfills the latter with multiple residents who work in 3~6 month periods, prevents the function as a father from burdening a single individual. This reflects the judgement that as a potential threat to the rights of the mother, who is the project initiator and owner of reproductive means, the father must be supervised. This does not mean the roles of mother and father as caregivers have been predetermined. The separate elements ‘mother’ and ‘father’ simply reconfirm the basic principle of sexual reproduction - that the mother requires assistance from others during the process of creating new life.</p>
					<br/>
					<p>Such program design is dictated not by Nahee’s arbitrary judgement, but by nahee.app, the upper category of Family.app. This signifies the separation of the functions as mother and administrator of the family system. Functionally speaking Daddy Residency divides ‘the person who makes the child’ and ‘the person who creates the household’ into separate entities. As an aggregation of various functions that derive from Nahee herself, nahee.app forms a brigade with its own distinct history and characteristics. Within the nahee.app website, Instagram page, and GitHub database numerous versions of the program are rewritten and modified. The pages initially operated as virtual folders collecting online resources relating to Nahee, and developed into an array of documents that recorded and analyzed her sensorimotor activities in sexual relations. Currently they have evolved to function as a kind of self-laboratory where the biotechnological interface that optimizes Nahee’s performance within the realm of social circumstance and sexual interaction is developed.</p>
					<br/>
					<p>For a certain period nahee.app remained in the asexual or oogenetic stage in which the artist’s alter egos - a category loosely encompassing both Nahee’s function as an artist and her creations - multiply. In this context Daddy Residency can be viewed as a newer stage of development, during which the self-referring activities of nahee.app are further opened towards the outside world. In the nahee.app universe, where forms of comfortable and satisfying sexual relations are explored with alternative personalities, ‘mother,’ ‘father,’ and ‘child’ are all unfamiliar concepts. By drafting the task of creating a family as her new project, Nahee has rewritten what has been considered a personal matter requiring the radical redefinition and expansion of self into a socio-economic inquiry that invites the active and firsthand participation of others. Within the project’s boundaries, complex imputes and contradictory emotions are entangled. As an outlet for the emotions activated by a pursuit of surprise and demand for stability, and as a virtual machine built to analyze and control such illogical black box, what kind of future can nahee.app bring forth?</p>
					<br/>
					<p>At this point Daddy Residency is in its early stages as a project, and is open in two directions. The first is towards a deeper and unknown speculative universe that results from a thought experiment performed by Nahee’s derivatives. The other is to directly confront the earthly world through a practical plan that can affect reality. Artistic practices such as writing, programming, exhibiting, lecturing, and performing mutually stimulate and protect this conflicting movement. However as the year 2026 and deadline for birth approaches, Daddy Residency will collide with legal, technical, and biological conditions that lie beyond the realm of art. The future of the child, or the future named child, depends on how much assistance Nahee can amass in the meantime. Help may assume the form of a person or artifact, and can be a corporeal entity or speculative object.</p>
					<br/>
					<p>This is not merely a science fiction setting. For example, the concept of ancestors as a collective deity is a speculative object, but affects reality through the corporeality of the ceremonial table set for traditional rites. Nahee inverts this structure and the direction of time by setting up a temporary stage summoning future children. The recent The Great Uterus series, branching out from Daddy Residency, investigates a future society that has built an integrated media system based on reproductive organs, and the use of reproductive cells as data carriers. As the technological usability of female reproductive cells is greater, the society has become a matriarchy, and follows the menstrual cycles of women like the rotation of seasons. The entire nation has evolved into a technological organism that is in sync with the time of the ‘The Great Uterus’ through the communication of cells. This parallel universe, named after Joanna Russ’s feminist utopia ‘Whileaway’, is a reverse image of the current technological patriarchy. It can also act as a pure glimpse into a future without any relation to the present, not even as a method of denial and transposition. The desire to investigate all horizons of possibility - environments that break free from what has always been and a judgement of what is right, wrong, or desirable - is a command that activates all of Nahee’s creations. If all possible forms of life rush towards us as a tide of future children, how must we receive them?</p>
					
					<br/><br/>
					<p>Translated by Soyoung Chong</p>
					<p>2021-02-15</p>
					</div>
					<br/>
				</div>
			</div>
		);
	}

    
}

export default OogenesisText

