import React, { Component , Fragment} from 'react';
import ReactDOM from 'react-dom'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  Link
} from 'react-router-dom';
import './App.scss';

import createClass from 'create-react-class';

import PlacentaNoClass from './components/PlacentaNoClass';
import PlacentaNo from './components/PlacentaNo';
import Ospapax from './components/Ospapax';
import WID from './components/WID';
import Oogenesis from './components/Oogenesis';
import Spermtank from './components/Spermtank';
import Cybersec from './components/Cybersec';
import Naheerun from './components/Naheerun';
import SubText from './components/SubText';

import Hydra from 'hydra-synth';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      momMounted:false,
      synth:undefined
    }

  }
   componentDidMount(){
    
    this.hydra = new Hydra({detectAudio :false});
    this.synth = this.hydra.synth

    // this.synth.s0.initImage("/ospapax.png")
    this.synth.src(this.synth.s0).out(this.synth.o3)
    this.synth.osc(1, 0, .1).color(.9, .8, 1).modulate(this.synth.o1).out(this.synth.o0)
    this.synth.osc(10).modulate(this.synth.o1).rotate(0.3).add(this.synth.o0).mult(this.synth.o3).modulate(this.synth.o1).rotate(.1).out(this.synth.o1)
    this.synth.render(this.synth.o1)
    this.setState({
      momMounted:true,
      synth:this.synth
    });
   }
    render() {
    return (
      <Router>
        <div id="app" className="App" style={{margin:"0px auto"}}>
          <Fragment>
              <Switch>
              <Route exact path="/test" exact render={() => (

                  <PlacentaNoClass mom={this.state.momMounted} synth={this.state.synth}></PlacentaNoClass>
                )} />
               <Route exact path="/" exact render={() => (

                  <PlacentaNo mom={this.state.momMounted} synth={this.state.synth}></PlacentaNo>
                )} />
                <Route exact path="/ospapax" exact render={() => (

                  <Ospapax mom={this.state.momMounted} synth={this.state.synth}></Ospapax>
                )} />
                <Route exact path="/what-is-daddy-residency" exact render={() => (

                  <WID mom={this.state.momMounted} synth={this.state.synth}></WID>
                )} />
                <Route exact path="/oogenesis" exact render={() => (

                  <Oogenesis mom={this.state.momMounted} synth={this.state.synth}></Oogenesis>
                )} />
                <Route exact path="/nahee-run-daddy" exact render={() => (

                  <Naheerun mom={this.state.momMounted} synth={this.state.synth}></Naheerun>
                )} />
                <Route exact path="/cybersecurity4uterus" exact render={() => (

                  <Cybersec mom={this.state.momMounted} synth={this.state.synth}></Cybersec>
                )} />
                <Route exact path="/would-you-be-my-sperm-tank" exact render={() => (

                  <Spermtank mom={this.state.momMounted} synth={this.state.synth}></Spermtank>
                )} />
            

                </Switch>
              </Fragment>
            </div>
          </Router>

        )}
}

export default App;
