import React from 'react';
import {withRouter} from 'react-router-dom';
import Details from './Details';
import Hydra from 'hydra-synth';

class Naheerun extends React.Component {

	constructor(props) {
		super(props);

  }
  componentDidUpdate(prevProps) {
	   if (this.props.mom !== prevProps.mom) {
	   		console.log("mom is here", this.props.mom);
			this.setDisplayCanvas();
	   }
	}
	componentDidMount(){
		if(this.props.mom){

		   		this.setDisplayCanvas();
		   }
	}
	setDisplayCanvas(){
		this.props.synth.s0.initImage("/bgImg/hydra/naheerun.jpg");
		document.getElementById('naheeCanvas').style.opacity=1
	}
	render() {

		return (
			<div>
			<Details 
				title={"naheerun"} 
				korTextTitle={"테세우스의 배"}
				korAuthor={"현호정"}
				engTextTitle={"Ship of Theseus"}
				engAuthor={"Ho-jeong Hyun "}
			/>
			</div>
		);
	}

    
}


export default withRouter(Naheerun)