import React from 'react';
import {withRouter} from 'react-router-dom';
import Details from './Details';

class Oogenesis extends React.Component {

	constructor(props) {
		super(props);

  }
  componentDidUpdate(prevProps) {
	   if (this.props.mom !== prevProps.mom) {
			this.setDisplayCanvas();
	   }
	}
	componentDidMount(){
		if(this.props.mom){
		   		this.setDisplayCanvas();
		   }
	}
	setDisplayCanvas(){
		this.props.synth.s0.initImage("/bgImg/hydra/oo-genesis.jpg");
		document.getElementById('naheeCanvas').style.opacity=1
	}

	render() {

		return (
			<div>
			<Details 
				title={"oogenesis"} 
				korTextTitle={"가능성의 창문들: 유성생식의 관점에서 예술 생산을 고쳐 쓰기"}
				korAuthor={"윤원화"}
				engTextTitle={"Windows of Possibilities: Rewriting Art Production with the Perspective of Sexual Reproduction"}
				engAuthor={"Wonhwa Yoon"}
			/>
			</div>
		);
	}

    
}


export default withRouter(Oogenesis)