import React from 'react';
import {withRouter} from 'react-router-dom';
import Details from './Details';

class WID extends React.Component {

	constructor(props) {
		super(props);

  }
  componentDidUpdate(prevProps) {
	   if (this.props.mom !== prevProps.mom) {
			this.setDisplayCanvas();
	   }
	}
	componentDidMount(){
		if(this.props.mom){
		   		this.setDisplayCanvas();
		   }
	}
	setDisplayCanvas(){
		this.props.synth.s0.initImage("/bgImg/hydra/what.jpg");
		document.getElementById('naheeCanvas').style.opacity=1
	}

	render() {
		return (
			<div>
			<Details 
				title={"WID"}
				korTextTitle={"대디 레지던시 모집 요강"}
				korAuthor={""}
				engTextTitle={"OPEN CALL FOR DADDIES"}
				engAuthor={""}
			/>
			</div>
		);
	}

    
}


export default withRouter(WID)