            <div className="interviewContent">
				그러게, 범주화해서 줄을 세우면 우리가 짐짓 기대한 결론이 나올까? 그 기대를 배반해도 당혹스러운 재미가 있겠지. 대디 레지던시는 nahee.app의 기획에서 출발해 콜렉티브 체제를 경유하기도 하면서 무정형적인 픽션들로 파생되고 있는데, 이 다양한 픽션의 생성 원리 자체가 이야기를 함께 만드는 자, 즉 콜렉티브를 닮은 것 같아서 재밌어. 아니, 대디 레지던시 자체가 다중 콜렉티브를 꾸리기 위한 시도 아닐까?
			</div>
