import React from 'react';
import {withRouter} from 'react-router-dom';
import Details from './Details';

class Ospapax extends React.Component {

	constructor(props) {
		super(props);

  }
  componentDidUpdate(prevProps) {
	   if (this.props.mom !== prevProps.mom) {
			this.setDisplayCanvas();
	   }
	}
	componentDidMount(){
		if(this.props.mom){
		   		this.setDisplayCanvas();
		   }
	}
	setDisplayCanvas(){
		this.props.synth.s0.initImage("/bgImg/hydra/ospapax.png");
		document.getElementById('naheeCanvas').style.opacity=1	
	}
	render() {

		return (
			<div>
			<Details 
				title={"ospapax"} 
				korTextTitle={"nahee.app의 작은 역사 2"}
				korAuthor={"nahee.app X 업체eobchae"}
				engTextTitle={"Part 2 - About Daddy Residency"}
				engAuthor={"nahee.app X 업체eobchae"}
			/>
			</div>
		);
	}

    
}


export default withRouter(Ospapax)