<p>“Sorry, if you felt so. Probably I should hurry to explain, before things escalate. We concluded that so-called notions of love, promise or family on which the human kind has been relaying, are too naïve and outdated. We are willing to organize communities where those notions will be replaced with more solid and enhanced ones. To achieve it, we need more of artificial beings who are interested and qualified in developing sexual reproduction, like you.”</p>
