import React from 'react';
import {withRouter} from 'react-router-dom';
import Details from './Details';

class Cybersec extends React.Component {

	constructor(props) {
		super(props);

  }
  componentDidUpdate(prevProps) {
	   if (this.props.mom !== prevProps.mom) {
			this.setDisplayCanvas();
	   }
	}
	componentDidMount(){
		if(this.props.mom){
		   		this.setDisplayCanvas();
		   }
	}
	setDisplayCanvas(){
		this.props.synth.s0.initImage("/bgImg/hydra/uteri-net.jpg");
		document.getElementById('naheeCanvas').style.opacity=1
	}

	render() {

		return (
			<div>
			<Details 
				title={"cybersec"}
				korTextTitle={"var theGreatUterus = []; // 대자궁이야기배열"}
				korAuthor={"nahee.app"}
				engTextTitle={"var theGreatUterus = []; // 대자궁이야기배열"}
				engAuthor={"nahee.app"}
			/>
			</div>
		);
	}

    
}


export default withRouter(Cybersec)